import React, { useState, useEffect } from 'react';
import './App.css';
import { Recipe } from './Recipe'

const App = () => {
	const APP_ID = '45c9f5b9';
	const APP_KEY = '4cd3b38a1da68d9ac7a3fe8628561b68';
	const apiRequest = `https://api.edamam.com/search?q=chicken&app_id=${APP_ID}&app_key=${APP_KEY}`

	const [ recipes, setRecipes] = useState([]);
	const [search, setSearch] = useState('');
	const [query, setQuery] =  useState('chicken')

	useEffect(() => {
	getRecipe(); 
	
  }, [query]);

  
  const getRecipe = async () => {
    const response = await fetch(`https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`);
    const data = await response.json();

    setRecipes(data.hits)
  }

  const updateSearch = e => {
	  setSearch(e.target.value)
	  
  };

	const getSearch = e => {
	  e.preventDefault();

	  setQuery(search)
	  setSearch('')
	  

	}

	return (
		<div className="App">
			<h1 className="header">Find Recipe</h1>
			<p className="header1">On search bar below, input a  type of food then click "Search" button</p>
			<form onSubmit={getSearch} className="search-form">
				<input
				 className="search-bar" 
				 type="text" 
				 value={search} 
				 onChange={updateSearch}  />
				<button className="search-button" type="submit">
					Search
				</button>
			</form>
			<div className='recipes'>
      {recipes.map(item => (
      <Recipe 
      key={Math.floor(Math.random()*2000)}
      title={item.recipe.label}
      calories={item.recipe.calories}
	  image={item.recipe.image}
	  ingredients={item.recipe.ingredients}
      />
      ))}
      </div>
		</div>
	);
};

export default App;
